import React from "react";
function Card(props) {
return (
    <>
    <div className="card">
    <img src={props.img_source} alt={props.alternate} className="img"></img>
    <div className="container">
    <h3>{props.m_name}</h3>
    <p>{props.owner}</p>
    <a href={props.watch} target="_blank">
    <button className="button">Watch Now</button>
    </a>
    </div>
    </div>
    </>
);
}
export default Card;


